import { useState, useEffect } from "react";
import "./SubSection.less";
import Grid2 from "@mui/material/Unstable_Grid2"; // Grid2 version 2
//@ts-ignore
import Fade from "react-reveal/Fade";
// @ts-ignore
import { getWindowDimensions, handleResize } from "../../common/windowDimensions";
import SummaryContent from "./SummaryContent";
import { IconButton } from "@mui/material";
import CloseIcon from "@mui/icons-material/Close";
import { Maybe } from "graphql/jsutils/Maybe";
import { SubsectionPreviewsCollection } from "../../__generated__/graphql";

interface SubSectionProps {
  show: boolean;
  backgroundColor: string;
  titleColor: string;
  onClick: () => void;
  // TODO remove ?
  subSectionContent?: Maybe<SubsectionPreviewsCollection> | undefined;
}
const SubSection: React.FC<SubSectionProps> = ({
  show,
  backgroundColor,
  titleColor,
  onClick,
  subSectionContent,
}) => {
  const [windowDimensions, setWindowDimensions] = useState(
    getWindowDimensions()
  );

  useEffect(() => {
    handleResize(setWindowDimensions);
  }, []);

  const mobileView = windowDimensions.width <= 800;
  const size = show ? "100%" : "0";

  const innerContainerStyles = show
    ? {
        // can customize
        width: "50vw",
      }
    : {
        width: "0",
      };

  const styles = mobileView
    ? {
        height: size,
      }
    : {
        width: size,
      };
  return (
    <div
      className="subsection__container"
      style={{
        backgroundColor: `${backgroundColor}`,
        color: `${titleColor}`,
        ...styles,
      }}
    >
      <div
        className="subsection__inner-container"
        style={{ ...innerContainerStyles }}
      >
        {show && (
          <Fade duration={900} delay={800}>
            <Grid2 container>
              <Grid2 xs={12} container marginTop="0.5em" marginBottom="0.5em">
                <Grid2
                  xs={12}
                  mdOffset="auto"
                  display="flex"
                  flexDirection="column"
                  alignItems="end"
                >
                  <IconButton
                    className="subsection__close-button"
                    onClick={onClick}
                    aria-label="fingerprint"
                    color="secondary"
                    style={{ color: titleColor }}
                  >
                    <CloseIcon />
                  </IconButton>
                </Grid2>
              </Grid2>
              {subSectionContent && show && (
                <SummaryContent subSectionContent={subSectionContent} />
              )}
            </Grid2>
          </Fade>
        )}
      </div>
    </div>
  );
};

export default SubSection;
